<template>
  <div class="container">
    <el-form
      ref="dataForm"
      :model="dataForm"
      :rules="rules"
      class="registerform"
      auto-complete="on"
      label-position="left"
      status-icon
    >
      <div class="title-container">
        <h3 class="title">注册</h3>
      </div>
      <el-form-item prop="name" label="用户名">
        <span class="svg-container">
          <svg-icon icon-class="user" />
        </span>
        <el-input
          ref="name"
          v-model="dataForm.name"
          placeholder="用户名"
          name="name"
          type="text"
          tabindex="1"
          auto-complete="off"
        />
      </el-form-item>
      <el-form-item prop="mobile" label="手机号">
        <span class="svg-container">
          <svg-icon icon-class="mobile" />
        </span>
        <el-input
          ref="mobile"
          v-model="dataForm.mobile"
          placeholder="手机号"
          name="mobile"
          type="text"
          tabindex="2"
          auto-complete="off"
        />
      </el-form-item>
      <el-form-item prop="password" label="密码">
        <span class="svg-container">
          <svg-icon icon-class="password" />
        </span>
        <el-input
          :key="passwordType"
          ref="password"
          v-model="dataForm.password"
          :type="passwordType"
          placeholder="密码"
          name="password"
          tabindex="3"
          auto-complete="off"
        />
        <span class="show-pwd" @click="showPwd">
          <svg-icon
            :icon-class="passwordType === 'password' ? 'eye' : 'eye-open'"
          />
        </span>
      </el-form-item>
      <el-form-item prop="checkPass" label="确认密码">
        <span class="svg-container">
          <svg-icon icon-class="password" />
        </span>
        <el-input
          :key="passwordType"
          ref="checkPass"
          v-model="dataForm.checkPass"
          :type="passwordType"
          placeholder="确认密码"
          name="checkPass"
          tabindex="4"
          auto-complete="off"
        />
        <span class="show-pwd" @click="showPwd">
          <svg-icon
            :icon-class="passwordType === 'password' ? 'eye' : 'eye-open'"
          />
        </span>
      </el-form-item>
      <el-button
        :loading="loading"
        type="primary"
        style="width:100%;margin-bottom:30px;"
        @click.native.prevent="submitForm"
        >注册
      </el-button>
    </el-form>
  </div>
</template>
<script>
import { register } from "@/api/user";
import { isPsw, isUserName, isMobile } from "@/utils/validate.js";

export default {
  data() {
    const checkName = (rule, value, callback) => {
      if (value === "") {
        return callback(new Error("用户名不能为空"));
      }
      if (value.length < 6 || value.length > 12 || !isUserName(value)) {
        return callback(
          new Error("用户名由6-12位的英文开头的英文和数字的组合")
        );
      }
      callback();
    };

    const checkMobile = (rule, value, callback) => {
      if (value === "") {
        return callback(new Error("手机不能为空"));
      } else {
        if (isMobile(value)) {
          callback();
        } else {
          return callback(new Error("请输入有效的手机号"));
        }
      }
    };
    const validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (!isPsw(value)) {
          callback(
            new Error("密码长度6-18位，数字、字母、字符至少包含其中两种")
          );
        }
        if (this.dataForm.checkPass !== "") {
          this.$refs.dataForm.validateField("checkPass");
        }
        callback();
      }
    };
    const validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.dataForm.password) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      dataForm: {
        name: "",
        password: "",
        checkPass: "",
        mobile: ""
      },
      rules: {
        name: [{ validator: checkName, trigger: "blur" }],
        password: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }],
        mobile: [{ validator: checkMobile, trigger: "blur" }]
      },
      loading: false,
      passwordType: "password"
    };
  },
  methods: {
    showPwd() {
      if (this.passwordType === "password") {
        this.passwordType = "";
      } else {
        this.passwordType = "password";
      }
      this.$nextTick(() => {
        this.$refs.password.focus();
      });
    },
    submitForm() {
      this.$refs.dataForm.validate(valid => {
        if (valid) {
          this.loading = true;
          register(this.dataForm).then(response => {
            if (response.code === 0) {
              this.$router.push({ path: this.redirect || "/" });
              this.loading = false;
            } else {
              this.$message({
                message: response.message,
                type: "warning"
              });
              this.loading = false;
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

